/* 全局样式 */
body, html {
    margin: 0;
    padding: 0;
    height: 100%; /* 使 body 和 html 占满屏幕 */
    display: flex;
    flex-direction: column;
    background: 
    linear-gradient(-180deg, #BCC5CE 0%, #929EAD 98%),
    radial-gradient(at top left, rgba(255,255,255,0.30) 0%, rgba(0,0,0,0.30) 100%);
    background-blend-mode: screen;
    background-attachment: fixed;
}

/* 登录页面样式 */
.login-container {
    max-width: 400px;
    margin: 100px auto;
    padding: 20px;
    background: rgba(255, 255, 255, 0.2);
    background-color: #ffffff;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);   
    text-align: center;
}

.login-container h2 {
    margin-bottom: 20px;
    color: #007bff;
}

.login-form input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
}

.login-form button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.login-form button:hover {
    background: linear-gradient(135deg, #007bff, #00bcd4);
    transition: all 0.3s ease;
}

.error-message {
    color: #d9534f;
    font-size: 14px;
}

/* 弹窗样式 */
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    backdrop-filter: blur(5px);
}

.popup-content {
    background: rgba(255, 255, 255, 0.9);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
    max-width: 300px;
    width: 90%; /* 响应式宽度 */
    min-width: 180px; /* 最小宽度 */
}

.popup-content h3 {
    margin-bottom: 10px;
}

.popup-content button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px;
}

.popup-content button:hover {
    background-color: #0056b3;
}

/* 左侧固定导航栏布局 */
.layout {
    display: flex;
    min-height: 100vh; /* 页面高度至少等于视口高度 */
}

/* 导航栏样式 */
.navbar {
    width: 150px; /* 固定宽度 */
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(10px);
    color: white;
    height: 100vh; /* 高度占满视口 */
    position: fixed; /* 固定位置 */
    top: 0;
    left: 0;
    z-index: 3;
    transition: all 0.3s ease;
}

.navbar-header {
    position: absolute;
    width: 150px;
    height: 50px;
    background-color: #a1ceff; 
    z-index: 3; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
}

/* 按钮样式 */
.exit-button {
    background-color: #a1ceff; 
    border: none;
    padding: 0;
    border-radius: 21px;
    cursor: pointer;
    z-index: 10;
}

/* 图片样式 */
.exit-button img {
    width: 42px;
    height: 42px;
    object-fit: contain; /* 确保图片保持原比例 */
    display: block;
}

.navbar ul {
    list-style-type: none;
    padding-top: 50px;
    padding-left: 0;
    margin: 0;
}

.navbar ul li {
    margin: 10px 0;
}

.navbar ul li a {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    display: block;
    transition: background-color 0.3s ease;
}

.navbar ul li a:hover {
    background: linear-gradient(135deg, #007bff, #00bcd4);
    border-radius: 4px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.navbar:hover .navbar-header{
    width: 180px;
    transition: width 0.3s ease; /* 动效：鼠标悬停时导航栏宽度增加 */
}

.navbar:hover{
    width: 180px;
    transition: width 0.3s ease; /* 动效：鼠标悬停时导航栏宽度增加 */
}

