/* 主内容区域样式 */
.content {
    margin-left: 150px; /* 为导航栏留出空间 */
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);   
    padding: 20px;
    width: calc(100% - 250px); /* 减去导航栏宽度 */
}

/* 外部容器 */
.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* 使 wrapper 高度至少等于视口高度 */
}

/* 主内容区域 */
main {
    flex: 1;  /* 使 main 占据剩余空间 */
    width: 1200px;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);   
    margin: 20px;
    padding: 20px;

}

/* 页头 */
header {
    background-color: #a1ceff;
    color: white;
    padding: 10px 0;
    text-align: center;
    -webkit-text-stroke: 1px #32003C;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 2;
}

header h1 {
    margin: 0;
    font-size: 24px;
    text-transform: uppercase;
}

/* 页脚 */
footer {
    text-align: center;
    background-color: #3a3a3a;
    color: white;
    padding: 10px;
    bottom: 0;      
    width: 100%;       
    z-index: 2;
}

/* 主容器样式 */
.container {
    margin: 20px;
    padding: 20px;
    max-width: 800px;
}

/* 卡片样式 */
.card {
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card h3 {
    margin-top: 0;
    color: #007bff;
}

/* 表单输入框样式 */
.card input, .card select, .card button {
    margin: 10px 0;
}

/* 列表样式 */
.card ul {
    list-style: none;
    padding: 0;
}

.card ul li {
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
}

.card ul li:last-child {
    border-bottom: none;
}

/* 容器样式 */
main {
    max-width: 1200px;
    margin: 20px auto;
    padding: 20px;
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* 表格样式 */
table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
}

table th, table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

table th {
    background-color: #007bff;
    color: white;
    font-weight: bold;
}

table tr:nth-child(even) {
    background-color: #f9f9f9;
}

table tr:hover {
    background-color: #f1f1f1;
}

/* 分页按钮样式 */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.pagination button {
    padding: 10px 20px;
    margin: 0 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.pagination button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.pagination span {
    font-size: 16px;
    margin: 0 10px;
}

/* 按钮样式 */
button {
    padding: 8px 16px;
    margin: 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:not(.delete-btn) {
    background-color: #007bff;
    color: white;
}

button:not(.delete-btn):hover {
    background-color: #0056b3;
}

button.delete-btn {
    margin-left: 10px;
    background-color: #d9534f;
    color: white;
}

button.delete-btn:hover {
    background-color: #c9302c;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* 输入框样式 */
input[type="text"], input[type="number"], select {
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    width: calc(100% - 24px);
    box-sizing: border-box;
}

input[type="text"]:focus, input[type="number"]:focus, select:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 4px rgba(0, 123, 255, 0.4);
}

/* 表单布局 */
.form-container {
    margin-bottom: 20px;
}

.form-container label {
    font-weight: bold;
    display: block;
    margin-bottom: 5px;
}
